let temp = 1
export default {
	data() {
		return {
			clientHeight1: '',
			clientHeight2: '',
			tabCurrentIndex: 0,
			swiperCurrentIndex: 0,
			avatarUrl: this.$store.state.userInfo.avatarUrl,
			customTheme: '',
			type: 0,
			orderTab: [{
					name: '普通订单',
					list: []
				},
				{
					name: '活动订单',
					list: []
				}
			],
			footOption: {
				checkAll: 'cuIcon-round',
				delete: false
			},
			//分页
			normalPage: 1,
			activityPage: 1
			//分页 end
		}
	},
	onLoad() {
		this.customTheme = this.$store.state.customTheme
		uni.getSystemInfo({
			success: res1 => {
				this.clientHeight1 = 800
			}
		})
		this.getOrderList()
		this.getPCCList() //亲子卡parent-child-card
	},
	methods: {
		//页面切换
		tabSelect(e) {
			let index = e.currentTarget.dataset.id
			this.swiperCurrentIndex = index
			this.tabCurrentIndex = index
		},
		swiperChange(e) {
			let index = e.detail.current;
			this.tabCurrentIndex = index;
			this.swiperCurrentIndex = index;
		},
		//页面切换 end
		//页面滑动到底部
		scrolltoBottom1() {
			if (temp) {
				temp = 0
				this.getOrderList()
				setTimeout(() => {
					temp = 1
				}, 1000);
			}
		},
		scrolltoBottom2() {
			if (temp) {
				temp = 0
				this.getPCCList()
				setTimeout(() => {
					temp = 1
				}, 1000);
			}
		},
		//页面滑动到底部 end
		getOrderList() {
			this.$http({
				url: '/order/list/' + this.normalPage,
			}).then(res => {
				for (let i of res) {
					this.orderTab[0].list.push(i)
				}
				this.normalPage += 1
				uni.getSystemInfo({
					success: res1 => {
						this.clientHeight1 = res1.windowHeight
					}
				})
			})
		},
		getPCCList() {
			this.$http({
				url: '/activity_order/list/' + this.activityPage,
				v3: true,
				loading: 'none'
			}).then(res => {
				for (let i of res) {
					this.orderTab[1].list.push(i)
				}
				this.activityPage += 1
				uni.getSystemInfo({
					success: res1 => {
						this.clientHeight2 = res1.windowHeight
					}
				})
			})
		},
		// setting() {
		// 	if (this.footOption.delete) {
		// 		this.footOption.delete = false
		// 	} else {
		// 		this.footOption.delete = true
		// 	}
		// },
		itemJump(index, id) {
			var index = index
			var id = id
			if (this.type == 2) {
				uni.navigateTo({
					url: `/pages/home/item?id=${id}&order_id=${this.list[index].order_id}`,
					animationType: 'pop-in',
					animationDuration: 200
				});
			} else {
				uni.navigateTo({
					url: `/pages/home/item?id=${id}`,
					animationType: 'pop-in',
					animationDuration: 200
				});
			}
		},
		refund(e, index) {
			this.$http({
				url: '/order/' + e + '/refund',
				method: 'PUT',
				loading: 'none'
			}).then(res => {
				this.list[index].status = -2
				uni.showToast({
					title: '已申请退款',
					duration: 2000,
				});
			})
		},
		itemOrderDetail(e, index) {
			uni.navigateTo({
				url: `/pages/user/order/item-order-detail?no=${e.out_trade_no}`
			});
		}
	}
}
